/** UI **/
.no-webp .home {
  position: relative;
  animation-name: slideNoWebp;
  animation-duration: 25s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.no-webp .home nav, .no-webp .home h1, .no-webp .home a, .no-webp .home .header-mobile-menu {
  z-index: 1;
}

.no-webp .home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: multiply;
}

@keyframes slideNoWebp {
  0% {
    background-image: url(../img/header.jpg);
    background-size: cover;
    background-position: center;
  }
  25% {
    background-image: url(../img/destacada.jpg);
    background-size: cover;
    background-position: center;
  }
  50% {
    background-image: url(../img/destacada2.jpg);
    background-size: cover;
    background-position: center;
  }
  75% {
    background-image: url(../img/destacada3.jpg);
    background-size: cover;
    background-position: center;
  }
  100% {
    background-image: url(../img/header.jpg);
    background-size: cover;
    background-position: center;
  }
}
.webp .home {
  position: relative;
  animation-name: slideWebp;
  animation-duration: 25s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.webp .home nav, .webp .home h1, .webp .home a, .webp .home .header-mobile-menu {
  z-index: 1;
}

.webp .home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: multiply;
}

@keyframes slideWebp {
  0% {
    background-image: url(../img/header.webp);
    background-size: cover;
    background-position: center;
  }
  25% {
    background-image: url(../img/destacada.webp);
    background-size: cover;
    background-position: center;
  }
  50% {
    background-image: url(../img/destacada2.webp);
    background-size: cover;
    background-position: center;
  }
  75% {
    background-image: url(../img/destacada3.webp);
    background-size: cover;
    background-position: center;
  }
  100% {
    background-image: url(../img/header.webp);
    background-size: cover;
    background-position: center;
  }
}
